import React, { Component } from "react";
import { Dropdown } from "semantic-ui-react";
import { Button } from "semantic-ui-react";
import "./product.css";

import {ShopContext} from "../../contexts/shop"


var GetSKU = require("../../services/inventory").get;
export default class ProductPage extends Component {
  state = { item: {} };
  handleChange = (e, { name, value }) => this.setState({ [name]: value });
  
  componentDidMount() {
    GetSKU(this.props.sku).then(item => {
      var sizeOptions = item.sizes.split(",").map(s => {
        return {
          text: s,
          value: s
        };
      });
      this.setState({ item, image: item.image, sizeOptions });
    });
  }
  render() {
    var item = this.state.item;
    return (
      <div className="productContainer">
        <div className="productLeft">
          <img alt=""
            style={{ height: "480px", width: "auto" }}
            src={this.state.image}
          />
          <div className="productTitle product">{item.displayName}</div>
          <div className="productDescription product">{item.description}</div>
        </div>
        <div className="productRight">
          <div style={{ display: "flex", flexDirection: "column" }}>
            <div
              style={{ cursor: "pointer" }}
              onClick={() => {
                this.setState({ image: item.image });
              }}
            >
              <img alt=""
                style={{ height: "120px", width: "auto" }}
                src={item.image}
              />
            </div>
            <div
              style={{ cursor: "pointer" }}
              onClick={() => {
                this.setState({ image: item.image2 });
              }}
            >
              <img alt=""
                style={{ height: "120px", width: "auto" }}
                src={item.image2}
              />
            </div>
            <div className="product productPrice">Kr. 150,00</div>
            <div className="productSize">
              <Dropdown
               name="size"
                placeholder="Vælg størrelse"
                fluid
                selection
                options={this.state.sizeOptions}
                onChange={this.handleChange}
              />
            </div>
            <div className="callForAction">
            <ShopContext>{
              (shop,add )=>
              <Button  disabled={!this.state.size} primary  onClick={()=>{
//debugger
add(this.state.item,this.state.size)
              }}>Læg i kurven {shop.items.length}</Button>}
              </ShopContext>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
