import React, { Component } from "react";
import { Button, Checkbox, Form, Icon, Step } from "semantic-ui-react";

import "./basket.css";
export default class Basket extends Component {
  state = {stage:1,ok:true};
  handleChange = (e, { name, value }) => this.setState({ [name]: value });
  _setStage(stage) {
    this.setState({ stage });
  }
  render() {
    return (
      <div className="basketContainer">
      
        <Step.Group>
          {/* <Step
            active={this.state.stage === 0}
            onClick={() => {
              this._setStage(0);
            }}
          >
            <Icon name="address card" />
            <Step.Content>
              <Step.Title>Lidt om dig</Step.Title>
              <Step.Description>Dit navn og hvor du bor</Step.Description>
            </Step.Content>
          </Step> */}
          <Step
            active={this.state.stage === 1}
            onClick={() => {
              this._setStage(1);
            }}
          >
            <Icon name="truck" />
            <Step.Content>
              <Step.Title>Levering</Step.Title>
              <Step.Description>Hvor og hvordan ønsker du leveret</Step.Description>
            </Step.Content>
          </Step>
          <Step
            active={this.state.stage === 2}
            onClick={() => {
              this._setStage(2);
            }}
          >
            <Icon name="payment" />
            <Step.Content>
              <Step.Title>Betaling</Step.Title>
              <Step.Description>Send penge</Step.Description>
            </Step.Content>
          </Step>

          <Step
          disabled={true}
            active={this.state.stage === 3}
            onClick={() => {
              this._setStage(3);
            }}
          >
            <Icon name="confirm" />
            <Step.Content>
              <Step.Title>Bekræftelse</Step.Title>
              <Step.Description>Hvornår får du varerne</Step.Description>
            </Step.Content>
          </Step>
        </Step.Group>

        {this.state.stage === 1 && (
          <Form>
            <Form.Group widths="equal">
              <Form.Input
                onChange={this.handleChange}
                label="Fornavn"
                name="firstName"
                placeholder="Indtast fornavn"
              />

              <Form.Input
                label="Efternavn"
                name="lastName"
                placeholder="Indtast efternavn"
                onChange={this.handleChange}
              />
            </Form.Group>
            <Form.Group widths="equal">
              <Form.Input
                label="Adresse"
                name="AdressLine1"
                placeholder="Indtast vej og husnummer + evt. etage"
                onChange={this.handleChange}
              />
            </Form.Group>
            <Form.Group widths="equal">
              <Form.Input
                name="zip"
                label="Postnummer"
                placeholder="Indtast postnummer"
              />

              <Form.Input
                label="By"
                name="city"
                placeholder="By"
                onChange={this.handleChange}
              />
            </Form.Group>
            <Form.Group widths="equal">
              <Form.Input
                name="phoneNumber"
                label="Telefonnummer"
                placeholder="Indtast telefonnummer"
                onChange={this.handleChange}
              />
            </Form.Group>
            <Form.TextArea
              name="comments"
              onChange={this.handleChange}
              label="Hvis du vil have det leveret et andet sted (f.eks. i skolen), så skriv det her i feltet eller SMS til 31 69 66 75 dit navn og ønsket leveringssted"
            />
             <Form.Checkbox
              name="accept"
              label="Jeg accepterer betingelserne"
              onChange={this.handleChange}
            />
          </Form>
        )}
       
             
       {this.state.stage === 2 && (
          <div>
            <img style={{height:"32px",width:"auto"}} src="https://i2.wp.com/www.californiawine.dk/wp-content/uploads/2018/09/mobile-pay-logo-2.png"/>
            <p>MobilePay xx,00 til 31 69 66 75!</p>
            <p><small>(Jeg er ikke klar til at tage imod kreditkort endnu)</small></p>

          

            <Form>
            <Form.Checkbox
              name="payed"
              label="Jeg har betalt"
              onChange={this.handleChange}
            />
           <Button disabled={!this.state.payed} type="submit">
             Send bestilling
           </Button>
         </Form>
          </div>
        )}
        {this.state.stage === 3 && (
        <div></div>
        )}
        
      </div>
    );
  }
}
